* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  font: inherit;
  line-height: inherit;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 16px;
  line-height: 1.5;
  background: #2C3A47;
  color: #fff;
}

.root {
  max-width: 1024px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  padding: 4rem 2rem;
}

main {
  flex-grow: 1;
}

h1, h2 {
  font-weight: 300;
}

h1 {
  font-size: 2em;
}

h2 {
  font-size: 1.6em;
}

h1 + h2 {
  margin-top: 2rem;
}

header {
  margin: auto;
  color: #fff;
}

a {
  color: #fff;
  text-decoration: underline;
}

a:hover {
  outline: .2em solid #fff;
  background: #fff;
  color: #242424;
  text-decoration: none;
}

code {
  background: #242424;
  color: #fff;
  padding: .2em .4em;
  border-radius: .2em;
}

button {
  display: block;
  white-space: nowrap;
  width: calc(50% - .5rem);
  padding: .5rem;
  background: #95afc0;
  border-radius: 4px;
  cursor: pointer;
  color: #fff;
  background: #2C3A47;
  transition: transform .32s ease-out;
  overflow: hidden;
  text-overflow: ellipsis;
}

button:not(:disabled):hover {
  transform: translateY(-3px);
  transition: transform .1s ease-in-out;
}

button:disabled {
  opacity: .8;
}

.services {
  width: 100%;
  padding: 2rem 0;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.service {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  flex-grow: 1;
  background: #fff;
  max-width: calc((100% / 3) - 1rem);
  min-height: 10rem;
  border-radius: 4px;
  box-shadow: 0 32px 32px rgba(0,0,0,.16);
}
